body {
  background-color: blue;
}

.title {
    font-family: Roboto, sans-serif;
}

.todo-list {
    background-color: white;
    margin: auto;
    width: 50%
    display flex;
    flex-direction: column;
    border: 1px solid #efefef;
    box-shadow:
    /* Top Layer Shadow*/
    0 1px 1px rgba(0,0,0.15),
    /* 2nd Layer*/
     0 10px 0 -5px #eee,
      /* 2nd Layer Shadow*/
       0 10px 1px -4px rgba(0,0,0,0.15),
       /* 3rd Layer */
       0 20px 0 -10px #eee,
       /* 3rd Layer Shadow*/
           0 20px 1px -9px rgba(0,0,0,0.15);
    padding: 50px
}

.todo-item {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 30px 20px 0;
    width: 70%;
    border-bottom: 1px solid #cecece;
    font-family: Roboto, sans-serif;
    font-weight: 100;
}

input[type=checkbox] {
    margin-right: 10px;
    font-size: 30;
}

input[type=checkbox]:focus {
    outline: 0;
}


